import React, { Component, PropTypes } from 'react'
import {Tree,Card} from 'antd';
const TreeNode = Tree.TreeNode;

const getTreeNode = (data,treenode) => {
	const children = data.children;
	return (
		<TreeNode title={"["+data.key+"] "} key={data.key}>
		{children && children.length>0 && children.map((innerData)=>getTreeNode(innerData))}
		</TreeNode>
	)
}
//{data.children && data.children.length>0?:""}

class DetailTableTree extends Component {
	render() {
		const {actions,datas,treenode,selTreenode} = this.props;
		////console.log("actions:",actions);
		////console.log("datas:",datas);
		return (
      <Card title="类型" style={{ width: "100%" }}>
          {datas.length>0 && 
          (<Tree onSelect={(selectedKeys)=>actions.setTree({selTreenode:selectedKeys[0]})}
              defaultSelectedKeys={[selTreenode]}
              defaultExpandedKeys={[datas[0].key]}
              >
            {datas.map((data)=>getTreeNode(data,treenode))}
          </Tree>)}
      </Card>
		)
	}

}

export default DetailTableTree;
