const findData = (key,datas) => {
    let ds = null;
    for(let i=0;i<datas.length&&ds===null;i++) {
        const data = datas[i];
        ////console.log("data.key:",data.key," key:",key," data.key===key:",data.key===key);
        if(data.key===key) {
            ds = data;
        }
        else {
            if(data.children && data.children.length>0) {
                const tmpDs = findData(key,data.children);
                if(tmpDs) {
                    ds = tmpDs;
                }
            }
        }
    }
    return ds;
}


const getDatasLoop = (datas)=>{
    let newDatas = new Array();
    datas.map((data)=>{
        ////console.log("data.children:",data.children);
        if(data.children && data.children.length>0) {
            ////console.log("data.children:",getDatasLoop(data.children));
            newDatas = newDatas.concat(getDatasLoop(data.children));
        }
        let newData = cloneDeep(data);
        delete newData["children"];
        newDatas.push(newData);
    });
    return newDatas;
}

const getInitDatas = (state,datas,pagination,params) => {
    const {searchValue,selTreenode,fatherKey} = state; //后续要处理查询条件
    const {treetable,treenode,rt} = state.config;
    const isTreenode = treetable && treenode && treenode!=="" && rt!==2;
    const tmpSelTreenode = isTreenode && selTreenode==="" && datas.length>0?datas[0].key:selTreenode;
    let fliterDatas = new Array();
    if(isTreenode) {
        //try {
        const data = findData(tmpSelTreenode,datas);
        ////console.log("data:",data);
       
        if(data!==null) {
             //console.log("initdatas 1");
            let childrens = data.children;
            let ds = cloneDeep(data);
            ds.children = false;
            
            fliterDatas.push(ds);
            ////console.log("222222",(childrens && childrens.length>0));
             if(childrens && childrens.length>0) {
                childrens.map((children)=>{
                    let chData = cloneDeep(children);
                    chData.children = false;
                     //console.log("initdatas 2");
                    fliterDatas.push(chData);
                });
            }
        }
       // }catch(e) {alert(e);}
    }
   // //console.log("33333",fatherKey);
    if(fatherKey!=0) {
        datas.map((data)=>{
           // //console.log("44444",data.fatherKey);
            if(data.fatherKey==fatherKey) {
                 //console.log("initdatas 3");
                fliterDatas.push(data);
            }
        });
    }
    if(""!= searchValue){
                    //编辑字段发现如果存在搜索条件，进行一次过滤
        let searchValueArr = searchValue.split(",");
        //console.log("searchValue",searchValue);
        for(let i=0;i<datas.length;i++) {
            const data = datas[i];
            let find = false;
            for(var p in data) {
                if(typeof(data[p])=="string") { 
                    for(let j=0;j<searchValueArr.length;j++) {
                        if(data[p].indexOf(searchValueArr[j])>=0) {
                            //console.log("data[p]",data[p],"searchValueArr[j])",searchValueArr[j]);
                            find = true;
                        }
                    }
                }
            }
            if(find) {
                fliterDatas.push(datas[i]);
            }
        }           
    }
    ////console.log("fliterDatas ",fliterDatas);
    return {
        datas:datas,
        pagination:pagination,
        fliterDatas:fliterDatas,
        fliterPagination:{
            pageSize:state.pagination.pageSize,
            total:fliterDatas.length,
            current:1
        },
        isTreenode:isTreenode,
        selTreenode:tmpSelTreenode
    }
}

const setChildrenDatas = (newData,datas)=>{
    let children = new Array();
    datas.map((data)=>{
        newData.key = newData.key?newData.key.toString():"";
        data.dbKey = data.dbKey?data.dbKey.toString():"";
        ////console.log("newData.key:",newData.key);
        ////console.log("data.dbKey:",data.dbKey);
        ////console.log("(newData.key&&newData.key!='0'):",(newData.key&&newData.key!="0"));
        const arr1 = (newData.key&&newData.key!="0")?newData.key.split("."):[];
        const arr2 = (data.dbKey&&data.dbKey!="0")?data.dbKey.split("."):[];
        ////console.log();
        if(data.dbKey.indexOf(newData.key)===0&&arr2.length===arr1.length+1) {
            let ds = {};
            data.wdfl.map((cell)=>{
                let value = cell.value || "";
                let valuespan = cell.valuespan || "";
                
                //新增整形浮点型过滤
//	            if(cell.fieldhtmltype == '1' && cell.type == '2'){
//	            	value = (!value || value=="")?0:parseInt(value);
//	            	valuespan = (!valuespan || valuespan=="")?0:parseInt(valuespan);
//	            }
//	            if(cell.fieldhtmltype == '1' && cell.type == '3'){
//	            	value = (!value || value=="")?0:parseFloat(value);
//	            	valuespan = (!valuespan || valuespan=="")?0:parseFloat(valuespan);
//	            }
				//字段类型处理，还原空，数字类型再行字段处理 -zxt 20170417
	            if(typeof newValue !== 'undefined' && newValue !== ''){
				    if(cell.fieldhtmltype == '1' && cell.type == '2') {
				        value = parseInt(value);
				        valuespan = parseInt(valuespan);
				    }
				    else if(cell.fieldhtmltype == '1' && cell.type == '3') {
				    	value = parseFloat(value);
				        valuespan = parseFloat(valuespan);
				    }
				}
	            //字段类型处理，还原空，数字类型再行字段处理 -zxt 20170417
	            
                if(cell.defaultValue && cell.defaultValue!="") {
                    if(cell.defaultValue=="[nowDate]") {
                        ds["wdfield"+cell.id] = Util.format(new Date(),"yyyy-MM-dd");
                        ds["wdfield"+cell.id+"span"] = Util.format(new Date(),"yyyy-MM-dd");
                    } 
                    else {
                        ds["wdfield"+cell.id] = cell.defaultValue;
                        ds["wdfield"+cell.id+"span"] = cell.defaultValue;
                    } 
                }
                else {
                    ds["wdfield"+cell.id] = value;
                    ds["wdfield"+cell.id+"span"] = valuespan;
                }
                //eval("ds.wdfield"+cell.id+"='"+cell.value+"'");
                //eval("ds.wdfield"+cell.id+"span='"+cell.valuespan+"'");
            });
            ds.dbId = data.dbId;
            ds.key = data.dbKey;
            ds.fatherKey = data.fatherDbKey;
            children.push(ds);
        }
    });
    if(children.length>0) {
        newData.children = children;
        newData.children = newData.children.map((data)=>{
            return setChildrenDatas(data,datas);
        });
    }
    return newData;
}

const getDatas4Tree = (datas) => {
    let rootDatas = new Array();
    datas.map((data,index)=>{
        let ds = {};
        data.wdfl.map((cell)=>{
            ////console.log("cell:",cell);
            let value = cell.value || "";
            let valuespan = cell.valuespan || "";
            
            //新增整形浮点型过滤
//          if(cell.fieldhtmltype == '1' && cell.type == '2'){
//          	value = (!value || value=="")?0:parseInt(value);
//          	valuespan = (!valuespan || valuespan=="")?0:parseInt(valuespan);
//          }
//          if(cell.fieldhtmltype == '1' && cell.type == '3'){
//          	value = (!value || value=="")?0:parseFloat(value);
//          	valuespan = (!valuespan || valuespan=="")?0:parseFloat(valuespan);
//          }
            //字段类型处理，还原空，数字类型再行字段处理 -zxt 20170417
            if(typeof newValue !== 'undefined' && newValue !== ''){
			    if(cell.fieldhtmltype == '1' && cell.type == '2') {
			        value = parseInt(value);
			        valuespan = parseInt(valuespan);
			    }
			    else if(cell.fieldhtmltype == '1' && cell.type == '3') {
			    	value = parseFloat(value);
			        valuespan = parseFloat(valuespan);
			    }
			}
            //字段类型处理，还原空，数字类型再行字段处理 -zxt 20170417
            
            if(cell.defaultValue && cell.defaultValue!="") {
                ////console.log("cell0:",cell);
                if(cell.defaultValue=="[nowDate]") {
                    ds["wdfield"+cell.id] = Util.format(new Date(),"yyyy-MM-dd");
                    ds["wdfield"+cell.id+"span"] = Util.format(new Date(),"yyyy-MM-dd");
                } 
                else {
                    ////console.log("cell1:",cell);
                    ds["wdfield"+cell.id] = cell.defaultValue;
                    ds["wdfield"+cell.id+"span"] = cell.defaultValue;
                } 
            }
            else {
                ds["wdfield"+cell.id] = value;
                ds["wdfield"+cell.id+"span"] = valuespan;
            }
            // eval("ds.wdfield"+cell.id+"='"+cell.value+"'");
//          eval("ds.wdfield"+cell.id+"span='"+cell.valuespan+"'");
            ////console.log("cell:",cell);
        });
        ////console.log("ds:",ds);
        ds.dbId = data.dbId;
        ////console.log("!data.dbKey:",!data.dbKey);
        if(!data.dbKey||data.dbKey==="0"||data.dbKey==="") {
            ds.key = index+1;
            ////console.log("ds1:",ds);
            rootDatas.push(ds);
        }
        else {
            data.dbKey = data.dbKey?data.dbKey.toString():"";
            ds.key = data.dbKey;
            ds.fatherKey = data.fatherDbKey;
            ////console.log("ds2:",ds);
            ////console.log("data.dbKey:",data.dbKey);
            let arr = data.dbKey?data.dbKey.split("."):[];
            if(arr.length===1) {
                rootDatas.push(ds);
            }
        }
    });
    let newDatas = new Array();
    newDatas = rootDatas.map((newData)=>{
        ////console.log("newData.dbId:",newData.dbId);
        if(!newData.dbId || newData.dbId==="0" || newData.dbId==="") {
            return newData;
        }
        else {
            return setChildrenDatas(newData,datas);
        }
        
    });
    return newDatas;
}

const getInitDatas4Tree = (state,datas) => {
    const {pageSize,current} = state.pagination;
    const newDatas = getDatas4Tree(datas);
    return getInitDatas(state,newDatas,{
        current:current,
        pageSize:pageSize,
        total:newDatas.length
    });
}

const editChildren = (params,data,heads,config) => {
    data.children = data.children.map((chData)=>{
        if(chData.key===params.key) {
            let dsTmp = objectAssign({},chData);
            dsTmp["wdfield"+params.fieldId] = params.value;
            dsTmp["wdfield"+params.fieldId+"span"] = params.valueSpan;
            heads.map((head)=>{
                ////console.log("head:",head);
                ////console.log("head:",head);
                doWfDetailFieldFliter(head,dsTmp);
            });
            dsTmp = doWfDetailCountRow(dsTmp,config);
            return dsTmp;
        }
        else if(chData.children && chData.children.length>0) {
            return editChildren(params,chData,heads,config);
        }
        else {
            return chData;
        }
    });
    return data;
}

const getDelChildrenRow = (data,selectedRowKeys) => {
    if(data.children && data.children.length>0) {
        const newDatas = new Array();
        data.children.map((chData)=>{
            if(selectedRowKeys.indexOf(chData.key)<0) {
                chData = getDelChildrenRow(chData,selectedRowKeys);
                newDatas.push(chData);
            }
        });
        if(newDatas.length>0) {
            data["children"] = newDatas;
        }
        else {
            delete data["children"];
        }
        //data["children"] = newDatas.length>0?newDatas:"";
        return data;
    }
    else {
        return data;
    }
}

const getAllDelDbIds = (data,delDbIds)=>{
    if(data.children && data.children.length>0) {
        data.children.map((chData)=>{
            if(chData.dbId && chData.dbId>0 && delDbIds.indexOf(chData.dbId)<0) {
                delDbIds.push(chData.dbId);
                getAllDelDbIds(chData,delDbIds);
            }
        });
    }
}

const getDelDbIds = (datas,selectedRowKeys,delDbIds)=> {
    datas.map((data)=>{
        ////console.log("selectedRowKeys:",selectedRowKeys," data.key:",data.key," data.dbId:",data.dbId," delDbIds.indexOf(data.dbId):",delDbIds.indexOf(data.dbId));
        if(selectedRowKeys.indexOf(data.key)>=0 && data.dbId && data.dbId>0 && delDbIds.indexOf(data.dbId)<0) {
            ////console.log("ok");
            delDbIds.push(data.dbId);
            getAllDelDbIds(data,delDbIds);
        }
        else {
            if(data.children && data.children.length>0) {
                getDelDbIds(data.children,selectedRowKeys,delDbIds);
            }
        }
        ////console.log("data.children:",data.children);
        
    });
    ////console.log("delDbIds:",delDbIds);
    return delDbIds;
}

const setChildren = (selectedRowKeys,data)=> {
    //if(data.children && data.children.length>0) {
    data.children = data.children.map((chData)=>{
        if(selectedRowKeys.indexOf(chData.key)>=0) {
            if(chData.children && chData.children.length>0) {
                let newChildren = chData.children;
                let theKey = chData.children[chData.children.length-1].key;
                theKey = theKey?theKey.toString():"";
                const arr = theKey.split(".");
                let newKey = "";
                ////console.log("arr.length:",arr.length);
                for(let i=0;i<arr.length-1;i++) {
                    ////console.log("arr[i]:",arr[i]);
                    newKey += arr[i]+".";
                }
                newKey += (parseInt(arr[arr.length-1])+1);
                ////console.log("newKey:",newKey," arr:",arr);
                newChildren.push({key:newKey});
                return objectAssign({},chData,{
                    children:newChildren
                });
            }
            else {
                return objectAssign({},chData,{
                    children:[{key:chData.key+".1"}]
                });
            }
        }
        else if(chData.children && chData.children.length>0) {
            return setChildren(selectedRowKeys,chData);
        }
        else {
            return chData;
        }
    });
    //}
    return data;
}

function doWfDetailFieldFliter(head,data) {
    const ft = head.fielddbtype;
    const ht = head.fieldhtmltype;
    const t = head.type;
    // if(t==="1"&&ht==="5") {
    //     return ;
    // }
    ////console.log("head:",head," data:",data," ft:",ft);
    let newValue = data["wdfield"+head.id];
    //if(!newValue || newValue=="") return ;
    ////console.log("head:",head);
    ////console.log("ft:",ft," newValue:",newValue);
    ////console.log("head:",head);
    ////console.log("ft:",ft);
	//  if(ft.indexOf("int")===0) {
	//      newValue = (!newValue || newValue=="")?0:parseInt(newValue);
	//  }
	//  else if(ft.indexOf("decimal")===0||ft.indexOf("number")===0) {
	//      newValue = (!newValue || newValue=="")?0:parseFloat(newValue);
	//  }
	////console.log('fielddbtype:',ft,'fieldhtmltype:',ht,'type:',t);
	//字段类型处理，还原空，数字类型再行字段处理 -zxt 20170417
	if(typeof newValue !== 'undefined' && newValue !== ''){
	    if(ht==1&&t==2) {
	        newValue = parseInt(newValue);
	    }
	    else if(ht==1&&t==3) {
	        newValue = parseFloat(newValue);
	    }
	    else if(ht==1&&t==5){
	    	newValue = newValue + "";
	        newValue = parseFloat(newValue.replace(/,/gi,""));
	    }
	}
    //字段类型处理，还原空，数字类型再行字段处理 -zxt 20170417
    data["wdfield"+head.id] = newValue;
    //return newValue;
}

function doWfDetailCountRow(data,config) {
    //console.log("行字段规则 ----- config:",config," data:",data);
    //zxt - 20170517 单独处理行字段数据类型计算问题 
    const { wfd, wdfl } = config;
    if(wfd && wfd.rowcalstr) { //行字段规则
        try {
            const rowCalStrArr = wfd.rowcalstr.split(";");
            let obj = {...data};
            let returnObj = {...data};
           // console.log("obj 1st  :",obj,"resutn obj 1st ",returnObj);
            for(let i=0;i<rowCalStrArr.length;i++) {
                const rowCalStr = rowCalStrArr[i].replace(new RegExp("detailfield_","gm"),"obj.wdfield");
                //let obj = data;
                wdfl.map(w => {
                	if(w.type == 2){
                		obj['wdfield' + w.id] = obj['wdfield' + w.id] ? parseInt(obj['wdfield' + w.id]) : 0;
                		obj['wdfield' + w.id + 'span'] = obj['wdfield' + w.id + 'span'] ? parseInt(obj['wdfield' + w.id + 'span']) : 0;
                	}
                	if(w.type == 3||w.type == 5){
                		obj['wdfield' + w.id] = obj['wdfield' + w.id] ? parseFloat(obj['wdfield' + w.id]) : 0;
                		obj['wdfield' + w.id + 'span'] = obj['wdfield' + w.id + 'span'] ? parseFloat(obj['wdfield' + w.id + 'span']) : 0;
                	}
                    
                    //console.log(" w id ",w.id,"     w id value :",obj['wdfield' + w.id]);
                });
              
                //eval(rowCalStr);
                let objKey = rowCalStr.split('=')[0].split('.')[1];
              //  console.log("objkey :",objKey);
                eval(rowCalStr);
                returnObj[objKey] = obj[objKey];
                wdfl.map(w => {
                    
                    if(w.type == 5){
                       // console.log("before value ", returnObj['wdfield' + w.id] ," type is :"+w.type ," id is :",w.id);
                        returnObj['wdfield' + w.id] = returnObj['wdfield' + w.id] ?  Util.formatNumber(parseFloat(returnObj['wdfield' + w.id]),parseInt(w.qfws),0) : 0;
                        returnObj['wdfield' + w.id+'span']=Util.formatNumber(parseFloat(returnObj['wdfield' + w.id]),parseInt(w.qfws),0);
                      //  console.log("after value ", returnObj['wdfield' + w.id]);
                    }
                    //console.log(" w id ",w.id,"     w id value :",obj['wdfield' + w.id]);
                });
                //returnObj[objKey + 'span'] = obj[objKey];
            }
           // console.log("returnObj end :",returnObj);
            return returnObj;
            //zxt - 20170517 单独处理行字段数据类型计算问题 
        }catch(e) {////console.log(e);
        	
        }
    }
    else {
        return data;
    }
}

const setDatasLoop = (datas,newDatas,config,heads) => {
    let tDatas = new Array();
    for(let i=0;i<datas.length;i++) {
        let data = datas[i];
        let dataBackup = cloneDeep(data);
        let find = false;
        ////console.log("dataBackup:",dataBackup);
        for(let j=0;j<newDatas.length&&!find;j++) {
            let newData = newDatas[j];   
            ////console.log("data.key:",data.key," newData.key:",newData.key);
            if(data.key===newData.key) {
                find = true;
                ////console.log("heads:",heads);
                ////console.log("heads:",heads," newData:",newData);
                data = objectAssign({}, data,newData);
                
                heads.map((head)=>{
                    ////console.log("head:",head);

                    doWfDetailFieldFliter(head,data);
                });
                ////console.log("dataBackup:",dataBackup);
                ////console.log("data:",data);
                ////console.log("newData:",newData);
            }
        }
        ////console.log("data:",data);
        if(dataBackup!==null && dataBackup.children && dataBackup.children.length>0) {
            ////console.log("setDatasLoop:",data.children);
            data.children = setDatasLoop(dataBackup.children,newDatas,config,heads);
        }
        data = doWfDetailCountRow(data,config);
        ////console.log("data:",data);
        tDatas.push(data);
        ////console.log("dyf data:",data);
    }
    ////console.log("dyf datas:",datas);
    return tDatas;
}


