//显示属性联动处理
export const doWfDetailEditTypeGet = (realEt,dn,record,fieldId,wll) => {
    if(!wll) return ;
		////console.log("wll :",wll," detailNum:",detailNum);
	for(let i=0;i<wll.length;i++) {
		const wl = wll[i];
		////console.log("wl.changefieldids:",wl.changefieldids," =?",fieldId+"_"+detailNum);
        const changefieldids = wl.changefieldids?wl.changefieldids.split(","):[];
        ////console.log("changefieldids:",changefieldids);
        for(let j=0;j<changefieldids.length;j++) {
			const cfieldId = changefieldids[j].split("_")[0];
            if(cfieldId===fieldId) {
				let selectField = wl.selectfieldid.split("_");
				let selectValue = "";
				if(selectField.length===2) {
					selectValue = parseInt(record["wdfield"+selectField[0]]);	
				}
				//eval("selectValue = record.wdfield"+(selectField.length==2?selectField[0]:""));
				////console.log(" wl.viewattr:",wl.viewattr," selectValue:",selectValue," wl.selectfieldvalue:",wl.selectfieldvalue," issame?:",selectValue==wl.selectfieldvalue);
				//selectValue && selectValue!="" && 
                ////console.log("selectValue:",selectValue," wl.selectfieldvalue:",wl.selectfieldvalue);
				if(selectValue===parseInt(wl.selectfieldvalue)) {
					////console.log("is 3?:",wl.viewattr);
					if(wl.viewattr==="1") { //编辑
						return 1;
					}
					else if(wl.viewattr==="2") { //必填
						return 2;
					}
					else if(wl.viewattr==="3") { //只读
						return 0;
					}
				}
			}
        }
	}
	//0 显示 1 编辑 2 必填
	return realEt;
}

let w = 9999;

//页面宽度适应
export const doWfDetailResize = (dn,width) => {
//	//console.log('doWfDetailResize: ',dn,width);
//  alert('dn'+dn+'width'+width)
    const w0 = jQuery(window).width()-30 || 9999;
    const w1 = jQuery("#wfDetail_"+dn).parent().width() || 9999;
    const w2 = jQuery('.excelMainTable').width() || jQuery('.maintable').width() || 9999;
    const w3 = jQuery('.excelOuterTable').width() || jQuery('.outerTable').width()|| 9999;
    let wm = w2 <= w0 ? w2 : (w3 <= w0 ? w3 : w0);
//  let wm = w1 <= w0 ? w1 : (w2 <= w0 ? w2 : (w3 <= w0 ? w3 : w0));
//  console.log('-----------');
    w = wm < w ? wm : w;
    w = w1 || window[`wfDetail_${dn}_width`];

//  console.log('w0: ',w0);
//  console.log('w1: ',w1);
//  console.log('w2: ',w2);
//  console.log('w3: ',w3);
//  console.log('wm: ',wm);
//  console.log('w: ',w);
//  alert('w: '+ w);
//  alert('w1: '+ w1);
//  alert('w2: '+ w2);
//  alert('w3: '+ w3);
//  console.log('width:',width);
    if(width) {
//      console.log('bool: ',width.indexOf('%'));
        if(width.indexOf('%') > 0){
            let newW = Number(width.split('%')[0])/100 * w;
//          console.log('------------newW:',newW)
            jQuery("#wfDetail_"+dn).width(0);
            jQuery(".ant-table-scroll-custom"+dn).width(newW);
        }else{
//          console.log('------------else width:',width)
            jQuery("#wfDetail_"+dn).width(0);
            jQuery(".ant-table-scroll-custom"+dn).width(width);
            jQuery(".ant-table-treenode"+dn).width(width-285);
        }
    }
    else if(w){
//      console.log('锛亀idth');
        jQuery("#wfDetail_"+dn).width(0);
        jQuery(".ant-table-scroll-custom"+dn).width(w);
        jQuery(".ant-table-treenode"+dn).width(w-285);
    }

}

//行列字段规则
export const doWfDetailCountCol = (wfd,datas,heads) => {
    if(!wfd) return ;
    if(!wfd.maincalstr) return ;
    const mainCalStrArr = wfd.maincalstr.split(";");
    for(let i=0;i<mainCalStrArr.length;i++) {
        const mainCalStr = mainCalStrArr[i].split("=");
        if(mainCalStr.length==2) {
            const fromField = mainCalStr[1].replace("detailfield_","wdfield"); //明细表
            const toField = mainCalStr[0].replace("mainfield_","field"); //主表表
            let haveField = false;
            for(let j=0;j<heads.length&&!haveField;j++) {
                if("wdfield"+heads[j].id==fromField) {
                    haveField = true;
                }
            }
            let sum = 0;
            for(let j=0;j<datas.length&&jQuery("#"+toField).length>0&&haveField;j++) {
                const data = datas[j];
                let cellValue = data[fromField];
                //eval("cellValue  = datas["+j+"]."+fromField);
                ////console.log("cellValue:",cellValue);
                // if(!cellValue&& cellValue!=0) haveField = false;
                // else {
                cellValue = (cellValue && cellValue!="")?parseFloat(cellValue):0;
                sum += cellValue;
                // }
            }
            //if(datas.length==0) haveField = false;
            ////console.log(" haveField :",haveField);
            if(jQuery("#"+toField).length>0&&haveField) {
                ////console.log("sum:",sum," dn:",dn," datas:",datas);
                sum =  sum.toFixed(2);
                jQuery("#"+toField).val(sum);
                if(jQuery("#"+toField).attr("type")=="hidden") {
                    jQuery("#"+toField+"span").html(sum);
                }
            }
        } 
    }
}

//行字段规则
export const doWfDetailCountRow = (data,config) => {
    ////console.log("config:",config," data:",data);
    //zxt - 20170517 单独处理行字段数据类型计算问题 
    const { wfd, wdfl } = config;
    if(wfd && wfd.rowcalstr) { //行字段规则
        try {
            const rowCalStrArr = wfd.rowcalstr.split(";");
            let obj = {...data};
            let returnObj = {...data};
            for(let i=0;i<rowCalStrArr.length;i++) {
                const rowCalStr = rowCalStrArr[i].replace(new RegExp("detailfield_","gm"),"obj.wdfield");
                //let obj = data;
                wdfl.map(w => {
                	if(w.type == 2){
                		obj['wdfield' + w.id] = obj['wdfield' + w.id] ? parseInt(obj['wdfield' + w.id]) : 0;
                		obj['wdfield' + w.id + 'span'] = obj['wdfield' + w.id + 'span'] ? parseInt(obj['wdfield' + w.id + 'span']) : 0;
                	}
                	if(w.type == 3){
                		obj['wdfield' + w.id] = obj['wdfield' + w.id] ? parseFloat(obj['wdfield' + w.id]) : 0;
                		obj['wdfield' + w.id + 'span'] = obj['wdfield' + w.id + 'span'] ? parseFloat(obj['wdfield' + w.id + 'span']) : 0;
                	}
                });
                ////console.log("行字段规则 ----- rowCalStr:",rowCalStr);
                //eval(rowCalStr);
                let objKey = rowCalStr.split('=')[0].split('.')[1];
                eval(rowCalStr);
                returnObj[objKey] = obj[objKey];
                returnObj[objKey + 'span'] = obj[objKey];
            }
            return returnObj;
            //zxt - 20170517 单独处理行字段数据类型计算问题 
        }catch(e) {
            //console.log(e);
        }
    }
    else {
        return data;
    }
}

export const getDateStr = (n) => {
    var date1 = new Date();
    ////console.log(date1.getFullYear()+"-"+(date1.getMonth()+1)+"-"+date1.getDate())
    var date2 = new Date(date1);
    date2.setDate(date1.getDate()+7);
    var times = date2.getFullYear()+"-"+(date2.getMonth()+1)+"-"+date2.getDate();
    return times;
}

export const format = (d,fmt) => {
    let o = {
        "M+": d.getMonth() + 1, //月份 
        "d+": d.getDate(), //日 
        "h+": d.getHours(), //小时 
        "m+": d.getMinutes(), //分 
        "s+": d.getSeconds(), //秒 
        "q+": Math.floor((d.getMonth() + 3) / 3), //季度 
        "S": d.getMilliseconds() //毫秒 
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (d.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (let k in o)
    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
}

