let sessionStorage = window.sessionStorage;
let localStorage = window.localStorage;

const Storage = {
	setWfDetailSyncStatus(dn,s) {
		let arr = [];
		if(sessionStorage.wfDetailSyncStatus) {
			arr = JSON.parse(sessionStorage.wfDetailSyncStatus);
		}	
		arr[dn] = s;
		sessionStorage.wfDetailSyncStatus = JSON.stringify(arr);
	},
	getWfDetailSyncStatus(dn) {
		if(sessionStorage.wfDetailSyncStatus) {
			const arr = JSON.parse(sessionStorage.wfDetailSyncStatus);
			return arr[dn];
		}
		else {
			return false;
		}
	},
	resetWfDetailSyncStatus() {
		if(sessionStorage.wfDetailSyncStatus) {
			sessionStorage.wfDetailSyncStatus = JSON.stringify([]);
		}
	},
	checkMarkWfDetailSyncDatas(dn) {
		let arr = [];
		let arr1 = [];
		if(sessionStorage.wfDetailSyncDatas) {
			arr = JSON.parse(sessionStorage.wfDetailSyncDatas);
		}	
		if(sessionStorage.markWfDetailSyncDatas) {
			arr1 = JSON.parse(sessionStorage.markWfDetailSyncDatas);
		}
		////console.log("arr["+dn+"]:",arr[dn]," arr1["+dn+"]:",arr1[dn]);
		////console.log("arr[dn] === arr1[dn]:",arr[dn] === arr1[dn]);
		////console.log("arr[dn] == arr1[dn]:",arr[dn] == arr1[dn]);
		return JSON.stringify(arr[dn]) === JSON.stringify(arr1[dn]);
	},
	markWfDetailSyncDatas() {
		sessionStorage.markWfDetailSyncDatas = sessionStorage.wfDetailSyncDatas;
	},
	setWfDetailSyncDatas(dn,s) {
		let arr = [];
		if(sessionStorage.wfDetailSyncDatas) {
			arr = JSON.parse(sessionStorage.wfDetailSyncDatas);
		}	
		arr[dn] = s;
		sessionStorage.wfDetailSyncDatas = JSON.stringify(arr);
	},
	getWfDetailSyncDatas(dn) {
		if(sessionStorage.wfDetailSyncDatas) {
			const arr = JSON.parse(sessionStorage.wfDetailSyncDatas);
			return arr[dn];
		}
		else {
			return false;
		}
	},
	setWfDetailNowDatas(dn,s,reqId) {
		// zxt - 20170517 sessionDatas 添加 reqId
		this.setWfDetailIsSave(dn,"1");
		//let arr = [];
		reqId == '-1' && sessionStorage.setItem(`${reqId}_${dn}_wfDetailNowDatas`,JSON.stringify(s));
		//arr[dn] = s;
		//sessionStorage.wfDetailNowDatas = JSON.stringify(arr);
	},
	getWfDetailNowDatas(dn,reqId) {
		// zxt - 20170517 sessionDatas 添加 reqId
		if(sessionStorage[`-1_${dn}_wfDetailNowDatas`]) {
			const dataString = sessionStorage[`-1_${dn}_wfDetailNowDatas`];
			sessionStorage.removeItem(`-1_${dn}_wfDetailNowDatas`);
			return JSON.parse(dataString);
		}else {
			return false;
		}
	},
	setWfDetailIsSave(dn,s) {
		let arr = [];
		if(sessionStorage.wfDetailIsSave) {
			arr = JSON.parse(sessionStorage.wfDetailIsSave);
		}	
		arr[dn] = s;
		sessionStorage.wfDetailIsSave = JSON.stringify(arr);
	},
	getWfDetailIsSave(dn) {
		if(sessionStorage.wfDetailIsSave) {
			const arr = JSON.parse(sessionStorage.wfDetailIsSave);
			return arr[dn];
		}
		else {
			return false;
		}
	},
	setBrowerDatas(dn,s) {
		let arr = new Object();
		if(sessionStorage.browerDatas) {
			arr = JSON.parse(sessionStorage.browerDatas);
		}	
		arr[dn] = s;
		////console.log("save arr:",arr);
		sessionStorage.browerDatas = JSON.stringify(arr);
	},
	getBrowerDatas(dn) {
		if(sessionStorage.browerDatas) {
			////console.log(sessionStorage.browerDatas);
			const arr = JSON.parse(sessionStorage.browerDatas);
			////console.log("arr:",arr);
			return arr[dn];
		}
		else {
			return false;
		}
	},
	setAutoSubmit(s) {
		////console.log("setAutoSubmit:",s);
		sessionStorage.autoSubmit = s;
	},
	getAutoSubmit() {
		return sessionStorage.autoSubmit;
	},
	setWfDetailExcelOut(dn,s) {
		let arr = [];
		//if(sessionStorage.wfDetailExcelOut) {
		//	arr = JSON.parse(sessionStorage.wfDetailExcelOut);
		//}	
		arr[dn] = s;
		////console.log("set arr:",arr,dn);
		sessionStorage.wfDetailExcelOut = JSON.stringify(arr);
	},
	getWfDetailExcelOut(dn) {
		if(sessionStorage.wfDetailExcelOut) {
			const arr = JSON.parse(sessionStorage.wfDetailExcelOut);
			////console.log("get arr:",arr,dn);
			return arr[dn];
		}
		else {
			return false;
		}
	}
};

window.wfDetailStorage = Storage;

export default Storage;


