import React from 'react'
import { render } from 'react-dom'
import { combineReducers,applyMiddleware,actionCreator,compose } from 'redux';
import { Provider } from 'react-redux'
import thunkMiddleware from 'redux-thunk'

import configureStore from './store/configureStore'
import rootReducer from './reducers/index'
import DetailTable from './components/DetailTable'
import * as APIS from './api/detail'
import * as types from './constants/ActionTypes'
import * as DetailActions from './actions'

export const createDetail = (detailId,callback) => {

	let store = configureStore(
		rootReducer,
		applyMiddleware(
			thunkMiddleware
		)
	);
	const debug = true;
	if(debug && !(window.attachEvent && navigator.userAgent.indexOf('Opera') === -1) && window.__REDUX_DEVTOOLS_EXTENSION__) { //非IE才有debug
		store = configureStore(
			rootReducer,
			compose(
			applyMiddleware(
				thunkMiddleware
			),window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__())
		)
	}
	//try {
	//alert(store);
	render(
		<Provider store={store}>
			<DetailTable />
		</Provider>,
		document.getElementById(detailId),
		() => {
			callback("success");
		}
	)
	//}catch(e) {alert("catch2:"+e);}
	return store;
}


let bindActionCreator = (actionCreator, dispatch) => {
  	return (...args) => dispatch(actionCreator(...args));
}

export const doAction = (obj,store,params,callback) => {
	try {
	const action = bindActionCreator(obj,store.dispatch);
	action(params,callback);
}catch(e) {alert("catch3:"+e);}
}


