import React, { Component, PropTypes } from 'react'
import { Modal, Button, Upload, message, Icon, Row, Col, Form, Radio, Alert, Progress } from 'antd';
const FormItem = Form.Item;
const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;

class DetailTableExcelImp extends Component {
  componentDidMount() {
    const {actions} = this.props;
    const {impTypeAuth} = this.props.config;
    let impTypeAuthArr = impTypeAuth && impTypeAuth!=""?JSON.parse(impTypeAuth):[0];
    let defaultValue = 0;
    if(impTypeAuthArr.indexOf(0)>=0) {
      defaultValue = 0;
    }
    else if(impTypeAuthArr.indexOf(1)>=0) {
      defaultValue = 1;  
    }
    else if(impTypeAuthArr.indexOf(2)>=0) {
      defaultValue = 2;
    }
    else {
      defaultValue = 0;
    }
    actions.changeImpType(defaultValue);
  }
  render() {
    const {impModal,impDatas,impNoDel,impPercent,impMsg} = this.props;
    const {actions,heads,config,pagination} = this.props;
    const {reqId,wfId,dn,impTypeAuth,tdatashowonly} = config;
    const {current} = pagination;
    const formItemLayout = {
         labelCol: { span: 8 },
         wrapperCol: { span: 16 }
      };
      ////console.log("impMsg:",impMsg);
      ///api/app/no0000012/detail/import
      const uploadProps = {
      name:"excelFile",
      showUploadList:false,
      action:'/cloudstore/app/no0000012/ControlServlet.jsp?action=Action_DoWfDetailExcelImport',
      data: {
        reqId:reqId,
        wfId:wfId,
        heads:JSON.stringify(heads),
        dn:dn
      },
      onChange(info) {
        ////console.log("info:",info);
        
        actions.uploadChange(info);
      }
    }
    ////console.log("impTypeAuth:",impTypeAuth);
    let impTypeAuthArr = impTypeAuth && impTypeAuth!=""?JSON.parse(impTypeAuth):[0];
    let defaultValue = 0;
    if(impTypeAuthArr.indexOf(0)>=0) {
      defaultValue = 0;
    }
    else if(impTypeAuthArr.indexOf(1)>=0) {
      defaultValue = 1;  
    }
    else if(impTypeAuthArr.indexOf(2)>=0) {
      defaultValue = 2;
    }
    else {
      defaultValue = 0;
    }
    ////console.log("impTypeAuthArr:",impTypeAuthArr);
    ////console.log("defaultValue:",defaultValue);
    return (
      <Modal title="明细导入" 
        visible={impModal}
        onCancel={()=>actions.hideImpModal()}
        footer={[
              (<Button key="back" type="ghost" size="large" onClick={()=>actions.hideImpModal()}>
                返 回
               </Button>),
              (<Button key="submit" type="primary" size="large" onClick={()=>{tdatashowonly ? actions.goPage({current}) : actions.impExcelDatas()}} 
                disabled={!impDatas || (impDatas && impDatas=="1") || (impDatas && impDatas!="1" && impDatas.length==0) || impMsg.length>0 }>
                确 认
               </Button>)
            ]}>
              <Form horizontal>
                <FormItem
                  {...formItemLayout}
                  label="导入模板：">
                  <a className="ant-form-text" onClick={()=>actions.detailExcelOut(false)} target="_blank">点击获取</a>
                </FormItem>
          <FormItem
                  {...formItemLayout}
                  label="选择文件："
                  required>
            <Upload {...uploadProps}>
              <Button type="ghost">
                  <Icon type="upload" /> 选择后自动读取
              </Button>
            </Upload>
                </FormItem>
              <FormItem
                {...formItemLayout}
                label="导入方式：">
                <RadioGroup onChange={(e)=>actions.changeImpType(e.target.value)} defaultValue={defaultValue}>
	                {impTypeAuthArr.indexOf(0)>=0 && <RadioButton value={0}>新增</RadioButton>}
	                {impTypeAuthArr.indexOf(1)>=0 && <RadioButton value={1}>更新</RadioButton>}
	                {impTypeAuthArr.indexOf(2)>=0 && <RadioButton value={2}>覆盖</RadioButton>}
	              </RadioGroup>
              </FormItem>
              <FormItem
		            {...formItemLayout}
		            label="进度：">
              	<Progress percent={impPercent} status={impDatas=="1"?"exception":(impPercent==100?"success":"active")} />
              </FormItem>
              {impMsg.length>0 && (
               	<FormItem
                  {...formItemLayout}
                  label="提示：">
                <Alert message={<div>
                  <p>导入数据异常，请修改后重试，具体信息如下：</p>
                  {impMsg.map((msg)=>{
                    return (<p>{msg}</p>)
                  })}
                  </div>} type="error" />
               </FormItem>
              )}
              {impDatas=="1" && (
                <FormItem
                  {...formItemLayout}
                  label="提示：">
                  <Alert message={<div>
                    <p>对不起，EXCEL读取异常，可能原因是：</p>
                    <p>（1）您读取的EXCEL数据过多，后台无法处理，需减少数据并进行分批导入；</p>
                    <p>（2）您的EXCEL格式有误！</p>
                    </div>} type="error" />
                </FormItem>
              )}
              {impDatas && impDatas!="1" && impDatas.length>0 && (
                <FormItem
                  {...formItemLayout}
                  label="提示：">
                  <Alert message={"EXECL读取成功，共读取"+impDatas.length+"条记录，点击确认即可同步到明细中！"} type="success" />
                </FormItem>
              )}
              <FormItem
                {...formItemLayout}
                label="说明：">
                <Alert message={<div>
                  <p>（1）建议每次导入数量不超过500条，可多次导入</p>
                  <p>（2）只允许导入有编辑权限的数据，如果没有编辑权限会自动忽略</p>
                  <p>（3）导入方式选择【新增】时，将会追加数据</p>
                  <p>（4）导入方式选择【更新】时，将只导入有主键数据，无主键数据自动忽略</p>
                  <p>（5）导入方式选择【覆盖】时，将会清空当前权限下已有数据</p>
                  <p>（6）对导入数据没有数字要求时，请尽量清空当前列的格式</p>
                  </div>} type="info" />
              </FormItem>
            </Form>
      </Modal>
    )
  }
}

export default DetailTableExcelImp;


