//        onChange={action4Value}
//        viewAttr={Number(et)+1}
//        hasAdvanceSerach
//        dataUrl = {`/api/workflow/browser01/data/162?`} //浏览框数据URL
//        completeURL={`/api/workflow/browser01/complete/162?`} //联想搜索URL
//        conditionURL={`/api/workflow/browser01/condition/162?`} //浏览框高级查询条件URL
//        modalStyle={{width:700,height:579,top:0}}
