export const initOutDatas = (params,callback) => {
	////console.log('initOutDatas : ',params)
	return (dispatch,getState) => {
		//zxt - 20170707 外部写入数据执行 dbId 不存在部分删除
		const { datas, config } = getState().detail;
		const { dn } = config;
		let delRowKey = [];
		datas.map(data => {
			const { key, dbId } = data;
			if(dbId){
				let del = true;
				params.newDatas.map(newData => {
					if(newData.dbId && newData.dbId === dbId){
						del = false;
					}
				});
				del && delRowKey.push(key);
			}
		});
		Storage.setWfDetailIsSave(dn);
		Storage.setAutoSubmit("");
		delRowKey.length > 0 && dispatch({type:types.DEL_ROW,params: { delRowKey }}); 
		//zxt - 20170707 外部写入数据执行 dbId 不存在部分删除
		
		dispatch({type:types.SET_DATAS,params});
		////console.log('initOutDatas-----wfTrigger',!params.ifNoTrigger);
		if(!params.ifNoTrigger) {
			if(typeof params.key == 'object' && params.key.length > 0){
				params.key.map(k => {
					dispatch(wfTrigger({key: k }));
				})
			}else{
				dispatch(wfTrigger({key: params.key}));
			}
			dispatch(doSqlTrigger({key: params.key}));
		}
		dispatch(doFnaTrigger({key: params.key}));
	}
}

