export const getPagesDatas = (params,callback) => {
	return (dispatch,getState) => {
		dispatch({type:types.START_LOADING});
		const {heads,datas,delDbIds,config} = getState().detail;
		const {reqId,wfId,dn} = config;
		API.doWfDetailSaveMultP({
			reqId,
			wfId,
			dn,
			heads,
			values:datas,
			type:"add",
			startFlag:0
		}).then(datas => {
			API.doWfDetailListGetP(params).then(res => {
				const { datas, current, total } = res;
				dispatch({type:types.INIT_DATAS,params:{ datas, current, total }});
				dispatch({type:types.END_LOADING});
			});
		})
	}
}
