export const addRow = (params) => {
	return (dispatch,getState) => {
		const {config} = getState().detail;
		const {dn,rt} = config;
		Storage.setWfDetailIsSave(dn);
		////console.log("addRow");
		Storage.setAutoSubmit("");
		params = {}
		dispatch({type:types.ADD_ROW,params}); //heads
		//有默认值之后自动出发联动 - zxt - 20170420
		const {datas} = getState().detail;
		if(rt!=0) {
			dispatch(wfTrigger({key:datas.length}));
			dispatch(doSqlTrigger({key:datas.length}));
			dispatch(doFnaTrigger({key:datas.length}));
		}
		else {
			dispatch(doFnaTrigger({key:datas.length}));
		}
		//有默认值之后自动出发联动 - zxt - 20170420 - 二开抓值可能会有问题。
		wfDetail.doAdd(dn,datas.length,datas.length-1,false);
		////console.log("datas add:",datas);
	}
}

