export const editCell = (params) => {
	return (dispatch,getState) => {
		//alert('editCell');
		const {dn,tol,rt} = getState().detail.config;
		////console.log("editCell ------ wfTrigger:",params,rt!=0);
		Storage.setWfDetailIsSave(dn); //标记
		////console.log("editCell");
		Storage.setAutoSubmit("");
		//console.log("editCell :",params);
		dispatch({type:types.EDIT_CELL,params:params}); //heads
		let find = false; //是否是触发字段
		for(let i=0;i<tol.length&&!find;i++) {
			const to = tol[i];
			if(to.triggerFieldName=="field"+params.fieldId) {
				find = true;
			}
		}
		////console.log('find:',find);
		if(find && rt!=0) {
			dispatch(wfTrigger({key:params.key,fieldId:params.fieldId}));
		}
//		//console.log('params: ', params)
		dispatch(doSqlTrigger({key:params.key,"fieldId":params.fieldId}));
		dispatch(doFnaTrigger({key:params.key}));
		wfDetail.doEdit(dn,params.key,params.fieldId);
	}
}

