const setDbIdsLoop = (datas,newDatas) => {
	for(let i=0;i<datas.length;i++) {
		let data = datas[i];
		let find = false;
		for(let j=0;j<newDatas.length&&!find;j++) {
			const newData = newDatas[j];
			if(data.key===parseInt(newData.id)) {
				find = true;
				data["dbId"] = newData["dbId"];
			}
		}
		if(!find && data.children && data.children.length>0) {
			data.children = setDbIdsLoop(data.children,newDatas);
		}
	}
	return datas;
}

export const saveDetail = (params,callback) => {
	return (dispatch,getState) => {
		dispatch({type:types.START_SAVELOADING});
		const {heads,datas,delDbIds,config} = getState().detail;
		const {reqId,wfId,dn} = config;
		if(!opQueue) opQueue = linkPromise();
		let allRead = true;
		heads.map(h=>{
			if(h.editType === '1' || h.editType === '2') allRead = false;
		})
		if(delDbIds && delDbIds.length>0) {
			opQueue = opQueue.then(()=>{
				return API.doWfDetailDelMultP({
					delDbIds,
					heads,
					reqId,
					wfId,
					dn
				});	
			});
		}
		if(!allRead){
			for(let i=0;i<datas.length;i+=100) {
				opQueue = opQueue.then((newDatas)=>{
					//alert(newDatas);
					//alert(typeof newDatas);
					////console.log("newDatas:",newDatas);
					if(typeof newDatas!="boolean" && newDatas && newDatas.length>0) {
					// 	alert(newDatas);
					// alert(typeof newDatas);
						let datasDbIds = datas;
						////console.log("datasDbIds:",datasDbIds," newDatas:",newDatas);
						datasDbIds = setDbIdsLoop(datasDbIds,newDatas); //保存ID
						
						//这里后续增加错误提示，如果dbids里面有<=0的数据报错 //mark
						dispatch({type:types.SET_DBID,params:{datas:datasDbIds}});
					}
					return API.doWfDetailSaveMultP({
						reqId,
						wfId,
						heads,
						dn,
						values:datas.slice(i,i+100),
						type:"add",
						startFlag:i
					});
				});
			}
			if(datas.length>0) {
				opQueue = opQueue.then((newDatas)=>{
					if(newDatas && newDatas.length>0) {
						let datasDbIds = datas;
						////console.log("datasDbIds:",datasDbIds," newDatas:",newDatas);
						datasDbIds = setDbIdsLoop(datasDbIds,newDatas); //保存ID
						dispatch({type:types.SET_DBID,params:{datas:datasDbIds}});
					}
					return linkPromise;
				});
			}
		}
		opQueue = opQueue.then(()=>{
			return new Promise((resolve)=>{
				//alert("ok1");
				Storage.setWfDetailIsSave(dn,"0");
				// zxt - 20170517 sessionDatas 添加 reqId
				Storage.setWfDetailNowDatas(dn,datas,reqId);
				//alert("ok2");
				////console.log("dn:",dn," datas:",datas);
				dispatch({type:types.END_SAVELOADING});
				callback();
				resolve(true);
			});
		}).catch((msg)=>{
			const show = "明细数据处理队列异常["+msg+"]，请联系管理员！";
			alert(show);
			////console.log(show);
			message.error(show);
		});
	}
}

