export const detailExcelOut = (needDatas)=> {
	return (dispatch,getState) => {
		//doWfDetailSave(false,params.dn);
		const {config,datas,outDbIds,heads} = getState().detail;
		////console.log("outDbIds:",outDbIds);
		const {reqId,wfId,dn,excelOutputAll} = config;
		dispatch({type:types.START_LOADING});
		////console.log("reqId:",reqId," needDatas:",needDatas);
		if(reqId!="-1"||!needDatas) {
			reqId != "-1" && dispatch(saveDetail({},()=>{}));
		    doDetailExcelOut({
		        reqId:reqId,
		        wfId:wfId,
		        heads:heads,
		        dn:dn,
		        outDbIds:needDatas?outDbIds:[""],
		        excelOutputAll:excelOutputAll?"0":"1"
		    });
		}
		else {
			// zxt - 20170517 sessionDatas 添加 reqId
			Storage.setWfDetailNowDatas(dn,datas,reqId);
			Storage.setWfDetailExcelOut(dn,"0");
			Storage.setAutoSubmit("2");
			doWfSave();
		}
		dispatch({type:types.END_LOADING});
	}
}

