const getDatasLoop = (datas)=>{
	let newDatas = new Array();
	datas.map((data)=>{
		////console.log("data.children:",data.children);
		if(data.children && data.children.length>0) {
			////console.log("data.children:",getDatasLoop(data.children));
			newDatas = newDatas.concat(getDatasLoop(data.children));
		}
		let newData = cloneDeep(data);
		delete newData["children"];
		newDatas.push(newData);
	});
	return newDatas;
}

const wfTrigger = (params) => {
	////console.log('-----------wfTrigger---------',params);
	return (dispatch,getState) => {
		const {config,heads,datas} = getState().detail;
		////console.log("config:",config);
		const {dn,wfId,tol,rt} = config;
		const datasLoop = getDatasLoop(datas);
		////console.log("datas:",datas);
		////console.log("datasLoop:",datasLoop);
		let triggerSet = [];
		//  trigger 分三种情况 ： -zxt 20170420
		//  全部联动 key == '' && !params.fieldId
		//  整行联动 key !== '' && !params.fieldId
		//  单个联动 key !== '' && params.fieldId
		if(params.fieldId){
			tol.map(h=>{
				h.triggerFieldName === "field"+params.fieldId && triggerSet.push(h);
			})
		}else{
			triggerSet = [].concat(tol);
		}
		if(!opQueue) opQueue = linkPromise();
		if(params.key==="") {
			for(let i=0;i<datasLoop.length;i+=100) {
				opQueue = opQueue.then((newDatas)=>{
					if(newDatas && newDatas.length>0) {
						dispatch({
							type:types.SET_TRIGGER,
							params:{
								datas:newDatas
							}
						});
					}
					// //console.log("in slice function ");
					// //console.log(datas);
					return API.doWfDetailTriggerP({
						wfId,
						heads,
						dn,
						triggerSet,
						datas:datasLoop.slice(i,i+100),
						startFlag:i
					});
				});
			}
			opQueue = opQueue.then((newDatas)=>{
				////console.log('wftg--------------',tol,newDatas);
				if(newDatas && newDatas.length>0) {
					dispatch({
						type:types.SET_TRIGGER,
						params:{
							datas:newDatas
						}
					});
//					for(let i=0;i<tol.length;i++) {
//						for(let j=0;j<newDatas.length;j++){
//							if(tol[i].triggerFieldName=="field"+newDatas[j].fieldId) {
//								rt!=0 && dispatch(wfTrigger({key:newDatas[j].key,fieldId:newDatas[j].fieldId}));
//							}
//						}
//					}
					Storage.setWfDetailIsSave(dn); //标记
					////console.log("wfTrigger");
					Storage.setAutoSubmit("");
				}
				return new Promise((resolve)=>{
					resolve(true);
				});
			});
		}
		else {
			// let flag = -1;
			////console.log("datas:",datas," params.key:",params.key,' params.fieldId:',params.fieldId);
			// datas.map((data,index)=>{
			// 	if(data.key===params.key) {
			// 		flag = index;
			// 	}
			// });
			// 
		
			opQueue = opQueue.then((msg)=>{
				return API.doWfDetailTriggerP({
					wfId,
					heads,
					dn,
					triggerSet,
					datas:datas,
					//[getDataByKey(params,datas)],
					startFlag:0
				});
			}).then((newDatas)=>{
				////console.log('wftg返回数据联动--------------',tol,newDatas);
				if(newDatas && newDatas.length>0) {
					dispatch({
						type:types.SET_TRIGGER,
						params:{
							datas:newDatas
						}
					});
					Storage.setWfDetailIsSave(dn); //标记
					////console.log("wfTrigger");
					Storage.setAutoSubmit("");
				}
//				for(let i=0;i<tol.length;i++) {
//					for(let j=0;j<newDatas.length;j++){
//						if(tol[i].triggerFieldName=="field"+newDatas[j].fieldId) {
//							rt!=0 && dispatch(wfTrigger({key:newDatas[j].key,fieldId:newDatas[j].fieldId}));
//						}
//					}
//				}
				tol && tol.map(t=>{
					newDatas.map(d=>{
						for(let key in d){
							"wd" + t.triggerFieldName == key && rt!=0 && dispatch(wfTrigger({key:d.key,fieldId:key.split('wdfield')[1]}));
							//"wd" + t.triggerFieldName == key && rt!=0 && d[key] !== "" && dispatch(wfTrigger({key:d.key,fieldId:key.split('wdfield')[1]}));
						}
					});
				});
				return new Promise((resolve)=>{
					resolve(true);
				});
			});
		}
		opQueue.catch((msg)=>{
			alert("字段联动异常："+msg);
			////console.log("字段联动异常：",msg);
		});
	}
}

const getDataByKey = (params,datas)=>{
	//console.log("+++++++");
	//console.log("datas:",datas);
	let returnData = null;
	datas.map((data)=>{
		if(returnData===null) {
			//console.log("data.key:",data.key);
			//console.log("params key:",params.key);
			if(data.key===params.key) {
			
				if(!!params.fieldId){
					returnData = {key:data.key,fatherKey:data.fatherKey};
					returnData['wdfield' + params.fieldId] = data['wdfield' + params.fieldId];
					returnData['wdfield' + params.fieldId + 'span'] = data['wdfield' + params.fieldId + 'span'];
				}else{
					returnData = data;
				}
			}

			else if(data.children && data.children.length>0) {
				returnData = getDataByKey(params,data.children);
			}
		}
		//console.log("returnData:",returnData);
		//console.log("-------");
	});
	////console.log("returnData:",returnData);
	return returnData;
}

let loadingInt;

export const uploadChange = (info)=>{
	return (dispatch,getState) => {
		////console.log("info:",info);
		const {impLoading} = getState().detail;
		const {status} = info.file;
		const response = info.file.response;
//		//console.log("info.file:",info.file);
//		//console.log("response:",response);
//		//console.log("status:",status);
		if(status==="uploading"&&!impLoading) {
			dispatch({type:types.START_IMPLOADING,params:{}});
			loadingInt = setInterval(function() {
	            dispatch({type:types.RUN_IMPPERCENT,params:{}});
	        },100);
		}
		if(response) {
			if(status==="error") {
				dispatch({type:types.SET_IMPDATAS,params:{
					datas:"1"
				}});
			}
			if(status==="done") {
				////console.log("response:",response);
				if(response && response.s_status=="false") {
					dispatch({type:types.SHOW_EXCELINPUTMSG,params:{impMsg:response.msg}});
				}
				else if(response.s_status=="true" && response.datas.length>0) {
					dispatch({type:types.SET_IMPDATAS,params:{
						datas:response.datas
					}});
				}
			}
			window.clearInterval(loadingInt);
		}
	}
}

export const impExcelDatas = ()=>{
	return (dispatch,getState) => {
		const {impDatas,config,impType} = getState().detail;
		const {dn,rt} = config;
		////console.log("impDatas:",impDatas);
		if(impDatas && impDatas!=="1" && impDatas.length>0) {
			////console.log("impDatas:",impDatas);
			dispatch({type:types.HIDE_IMPMODAL,params:{}});
			setTimeout(()=>{
				const oldDatas = getState().detail.datas;
				dispatch({type:types.DO_EXCELINPUT,params:{
					impDatas:impDatas
				}});

				Storage.setWfDetailIsSave(dn); //标记
				////console.log("impExcelDatas");
				Storage.setAutoSubmit("");
				//判断是否有联动
				if(rt!=0) {
					dispatch(wfTrigger({key:""}));
					dispatch(doSqlTrigger({key:""}));
					dispatch(doFnaTrigger({key:""}));
				}
				else {
					dispatch(doFnaTrigger({key:""}));
				}
				// 新增导出 外部API -zxt 20170414
				let startKey = impType == 0 ? (oldDatas.length + 1) : 1;
				let keys = [];
				impDatas.map((data,i)=>{
					keys.push(startKey + i);
				})
				const { datas } = getState().detail;
				wfDetail.doImport(dn,datas,keys);
				// 新增导出 外部API -zxt 20170414
			},300);
		}
	}
}

