export const doSqlTrigger = (params)=>{
	return (dispatch,getState) => {
		////console.log("doSqlTrigger",params);
		const {fieldId,key} = params;
		//if(key=="") {
		const {config,datas,heads} = getState().detail;
		////console.log("getState().detail:",getState().detail);
		const {wfl,reqId} = config;
		if(!wfl) return;
		//找出所有联动字段，替换sql主表参数
		console.log('doSqlTrigger __ wfl : ',wfl);
		let triSqlArr = new Array();
		let triDateArr = new Array();
		for(let i=0;i<heads.length;i++) {
			let head = heads[i]; //cloneDeep
			if(head.cdata!=="") {
				let cdataFields = new Array();
				let cdataDateFields = new Array();
				let cdata = Base64.decode(head.cdata);
				for(let j=0;j<wfl.length;j++) {
					let obj = wfl[j];
					if(cdata.indexOf("$"+obj.id+"$")>=0) {
						// if(obj.detailtable=="") {
						// 	const mainObj = document.getElementById("field"+obj.id);
						// 	obj.value = mainObj?mainObj.value:"";
						// 	cdata = cdata.replace("$"+obj.id+"$",obj.value);
						// 	//console.log("cdata:",cdata);
						// 	head.cdata = Base64.encode(cdata);
						// }
						cdata.indexOf("doFieldDate") >= 0 ? cdataDateFields.push(obj) : cdataFields.push(obj);
					}
				}
				head.cdataFields = cdataFields;
				head.cdataDateFields = cdataDateFields;
				cdata.indexOf("doFieldDate") >= 0 ? triDateArr.push(head) : triSqlArr.push(head);
			}
		}
		triSqlArr.sort(by("cdataOrder"));
		////console.log("triSqlArr:",triSqlArr);
		//替换sql明细表参数
		////console.log('triDateArr: ',triDateArr);
		if(!opQueue) opQueue = linkPromise(); //获得队列
//		//console.log('out ---- key: ' ,key, 'params:',params)
//		//console.log("doSqlTrigger __ triSqlArr:",triSqlArr);
		let newDatas = cloneDeep(datas);
		// 日期联动 - zxt 20170918
		triDateArr.map(triDate => {
			let cdata = Base64.decode(triDate.cdata);
			let cdataDateFields = triDate.cdataDateFields;
			let _cdata = cdata.replace(new RegExp(" ","g"),'');
			_cdata = _cdata.replace(new RegExp("\"","g"),'');
			_cdata = _cdata.replace(new RegExp("\'","g"),'');
			_cdata = _cdata.split('doFieldDate(')[1].split(')')[0];
			let __cdata = _cdata.match(new RegExp(/\$\d+(\$\-?\+?\$\d+)+\$/,'g'),'')[0];
			////console.log('cdataDateFields:', cdataDateFields);
			newDatas = newDatas.map((data, index) => {
				if(!key || key === index + 1){
					let setData = false, 
						newCdata = __cdata;
					cdataDateFields.map(filed => {
						if((fieldId && fieldId === filed.id) || !fieldId) setData = true;
					})
					console.log("newCdata 1:"+newCdata);
					cdataDateFields.map(filed => {
						////console.log('!data[`wdfield${filed.id}`]', filed, !data[`wdfield${filed.id}`])
						if(!data[`wdfield${filed.id}`]){
							setData = false;
						}else if(data[`wdfield${filed.id}`] && setData){
							newCdata = newCdata.replace(`$${filed.id}$`, data[`wdfield${filed.id}`] ? new Date(data[`wdfield${filed.id}`]).getTime() : 0);
							console.log("newCdata 2:"+newCdata);
						}
					})
					if(setData){
						let _data = {...data};
						_data[`wdfield${triDate.id}`] =eval(_cdata.replace(__cdata, Math.ceil(eval(newCdata) / 24000 / 3600)));
						return _data
					}
					return data
				}
				return data
			});
		});
		// //console.log('datas , newDatas:', datas, newDatas);
		triDateArr.length > 0 && dispatch({
			type:types.SET_TRIGGER,
			params:{
				datas:newDatas
			}
		});
		// 日期联动 - zxt 20170918
		console.log('triSqlArr __ wfl : ',triSqlArr);
		for(let k=0;k<triSqlArr.length;k++) {
			let cdata = Base64.decode(triSqlArr[k].cdata);
			let cdataFields = triSqlArr[k].cdataFields;
			console.log('1cdata: ' ,cdata);
			console.log('triSqlArr ---- key: ' ,key, 'params:',params)
			for(let i=0;i<datas.length;i+=100) {
				opQueue = opQueue.then((msg)=>{
					let triggerSqls = new Array();
					for(let j=i;j<i+100&&j<datas.length;j++) {
						let data = datas[j];
						// 新增keys -zxt 20170414
						console.log('typeof key: ' ,typeof key," length",key.length);
						if(typeof key == 'object' && key.length > 0){
							key.map(kIn =>{
								let newCdata = '';
								if(kIn==data.key) {
									////console.log('-----cdataFields:',cdataFields);
									for(let m=0;m<cdataFields.length;m++) { //替换所有字段
										if(cdataFields[m].detailtable!="") {
											
											let obj = data["wdfield"+cdataFields[m].id];
											obj = obj?obj.toString():"";
											console.log("newCdata 3:"+newCdata);
											newCdata = cdata.replace("$"+cdataFields[m].id+"$",obj?`'${obj}'`:"''");
											console.log("newCdata 4:"+newCdata);
										}
										else {
											const mainObj = document.getElementById("field"+cdataFields[m].id);
											console.log("newCdata 5:"+newCdata);
											newCdata = cdata.replace("$"+cdataFields[m].id+"$",mainObj?`'${mainObj}'`.value:"''");
											console.log("newCdata 6:"+newCdata);
										}
									}
									//console.log("doSqlTrigger __ cdata:",newCdata," k:",k);
									let triggerSql = {
										id:triSqlArr[k].id,
										key:data.key,
										str:Base64.encode(newCdata),
										value:"",
										flag:j
									}
									triggerSqls.push(triggerSql);
								}
							})
						// 新增keys -zxt 20170414
						}else{
							if(key==""||key==data.key) {
								var newCdata = cdata;
								for(let m=0;m<cdataFields.length;m++) { //替换所有字段
									if(cdataFields[m].detailtable!="") {
									
										let obj = data["wdfield"+cdataFields[m].id];
									
										obj = obj?obj.toString():"";
										
										newCdata = newCdata.replace("$"+cdataFields[m].id+"$",obj?obj:"''");
										console.log("newCdata 8:"+newCdata);
									}
									else {
										const mainObj = document.getElementById("field"+cdataFields[m].id);
										
										newCdata = newCdata.replace("$"+cdataFields[m].id+"$",mainObj?mainObj.value:"''");
									
									}
								}
							
								let triggerSql = {
									id:triSqlArr[k].id,
									key:data.key,
									str:Base64.encode(newCdata),
									value:"",
									flag:j
								}
								triggerSqls.push(triggerSql);
							}
						}
					}
					if(triggerSqls.length>0)
						return API.doWfDetailSqlTriggerP({triggerSqls,reqId});
					else
						return [];
				}).then((thedatas)=>{
					////console.log("thedatas:",thedatas);
					for(let j=0;j<thedatas.length;j++) {
						let obj = thedatas[j];
						let newObj = newDatas[obj.flag];
						newObj["wdfield"+obj.id] = obj.value;
						newObj["wdfield"+obj.id+"span"] = obj.valuespan;
					}
				});
			}
			opQueue.then((msg)=>{
				////console.log("doSqlTrigger __ newDatas:",newDatas);
				////console.log("datas:",datas," newDatas:",newDatas);
				dispatch({
					type:types.SET_TRIGGER,
					params:{
						datas:newDatas
					}
				});
			});
			opQueue.catch((e)=>{
				////console.log("sql属性联动异常："+e);
				alert("sql属性联动异常："+e);
			});
		}
		//}

	}
}


